
import XButton from 'vux/src/components/x-button/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'

import house from '../lib/house.js';

export default {
components: {XButton},
data() {
return {
myAddress: null,
addressTimer: null,
houses: []
}
},
beforeRouteLeave(to, from, next) {
clearTimeout(this.addressTimer);
next()
},
mounted() {
this.initAddress();
},
methods: {
initAddress() {
this.myAddress = this.$nasTool.myAddress;
this.addressTimer = setTimeout(() => {
this.initAddress();
}, 1000);
},
addHouse() {
this.$router.push({
name: 'addHouse'
})
},
initMyHouse() {
var rs = [];

this.$vux.loading.show({
text: '....'
});
house.getHouses((data) => {
this.$vux.loading.hide()
if (data && data.houses) {
data.houses.forEach(h => {
if (h.fromUser == this.myAddress) {
rs.push(h);
}
})
}
});
this.houses = rs;
},
getMyAddress() {
this.$nasTool.pay({
func: 'getAccount',

}, (res) => {
if (res.code == '0') {
this.myAddress = this.$nasTool.myAddress;
}
})
},
typeName(type) {
var name = null;
this.rentHouseTypes.forEach(rh => {
if (rh.key == type) {
name = rh.value;
return false;
}
});
return name;
},
getAddressNames(address) {
var addressNames = [];
if (address && address.length > 0) {
var map = {};
var length = address.length;
address.forEach(ad => {
map[ad] = true;
});
var nameMap = {};
ChinaAddressV4Data.forEach(cv => {
if (length == 0) {
return false;
}
if (map[cv.value]) {
nameMap[cv.value] = cv.name;
delete map[cv.value];
length--;
}
});

address.forEach(ad => {
var name = nameMap[ad];
if(name){
addressNames.push(name);
}
});
}
return addressNames.join(' ');
},
getTimeStr(str){
return str?moment(str).format('YYYY-MM-DD hh:mm'):''
},
toDetail(houseId){
this.$router.push({
name:'detail',
query:{
id:houseId
}
})
}
},
computed: {
rentHouseTypes() {
return this.$store.state.rentHouseTypes;
},
},
watch: {
myAddress(newVal, oldVal) {
console.log('', oldVal, '-', newVal)
this.initMyHouse();
}
}
}



